




















import Vue from 'vue';
import { getImg, transformSocialLink, VALID_SOCIALS } from '@/services/common';

export default Vue.extend({
  name: 'ContactInfo',
  props: {
    contact: {
      type: Object,
      required: true,
    },
  },
  computed: {
    orderedSocials() {
      return Object.keys(this.contact.socials)
        .sort()
        .filter((site: string) => VALID_SOCIALS.indexOf(site) > -1)
        .slice(0, 5);
    },
  },
  methods: {
    getImg,
    transformSocialLink,
  },
});
